@use 'sass:color';

[data-world] {
  margin: 0;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  body.admin & {
    background-image: url(../../../images/background.jpg);
    [data-country] text {
      display: none;
    }
  }

  [data-map] {
    filter: drop-shadow(#{rem-calc(2)} #{rem-calc(4)} #{rem-calc(4)} rgba(0, 0, 0, 0.5));
    //Slight adjustments to account for the visual scaling on "touch"
    #africa [data-country] text {
      font-size: rem-calc(10);
    }

    #europe [data-country] text {
      font-size: rem-calc(8);
    }
    
    #oceania [data-country] text {
      font-size: rem-calc(8);
    }

    #north-america [data-country] text {
      font-size: rem-calc(10);
    }

    #south-america [data-country] text {
      font-size: rem-calc(9);
    }

    > [data-country]:not(.active) {
      fill: $aluminum;
      pointer-events: none;
    }

    &[style*="transform"] {
      filter: drop-shadow(#{rem-calc(2)} #{rem-calc(4)} #{rem-calc(4)} rgba(0, 0, 0, 0));
      > .island, 
      > [data-country].disabled {
        opacity: 0;
      }
    }

    [data-continent] {
      fill: $aluminum;
      pointer-events: none;
      &.active {
        cursor: pointer;
        pointer-events: all;
        &:not(.selected) {
          &:focus, 
          &:hover {
            [data-country] {
              &.active {
                &,
                &.programs, 
                &.suspended {
                  path, polygon {
                    fill: $secondary;
                    stroke: $secondary;
                  }
                }
              }
            }
          }

          [data-country] {
            &.active {
              path, polygon {
                fill: $white;
              }
              &.suspended {
                path, polygon {
                  fill: $smoke;
                }
              }
              &.programs {
                path, polygon {
                  fill: $primary;
                }
              }
            }
            &.disabled {
              cursor: not-allowed;
              pointer-events: none;
              path, polygon {
                fill: $aluminum;
              }
            }
          }
        }
        &.selected {
          [data-country] {
            @each $key, $color in $highlights {
              $c: color.scale($primary, $lightness: $color, $saturation: -80%);
              &.opacity-#{$key} {
                path, polygon {
                  fill: $c
                }
                &.selected {
                  path, polygon {
                    fill: $secondary;
                  }
                }
                &.suspended {
                  path, polygon {
                    fill: grayscale($c)
                  }
                }
              }
            }
            &.active {
              cursor: pointer;
              &.selected {
                pointer-events: none;
                cursor: help;
              }
              &.suspended, &:not(.programs) {
                path, polygon {
                  fill: grayscale($secondary)
                }
                @each $key, $color in $highlights {
                  $c: color.scale($primary, $lightness: $color, $saturation: -80%);
                  &.opacity-#{$key} {
                    path, polygon {
                      fill: grayscale($c)
                    }
                  }
                }
              }
              &:focus, 
              &:hover {
                &[class*="opacity-"] path, 
                &[class*="opacity-"] polygon {
                  fill: $secondary;
                  stroke: $secondary;
                }
                &.suspended path, 
                &.suspended polygon {
                  fill: grayscale($secondary);
                  stroke: grayscale($secondary);
                }
              }
              text, line {
                opacity: 1
              }
            }
            &:not(.active), &:not(.programs) {
              cursor: default;
              pointer-events: none;
            }
          }
        }
        &.disabled {
          cursor: not-allowed;
          pointer-events: none;
          [data-country] {
            opacity: 0;
          }
          path, polygon {
            fill: $smoke;
          }
        }
      }
      [data-country] {
        text, line {
          font-weight: $font-weight-normal;
          text-transform: uppercase;
          font-size: rem-calc(13);
          fill: $primary;
          opacity: 0;
          &.text-invert {
            fill: $white
          }
        }
      }
    }
  }
}